import React from 'react'
import { useGlobalContext } from './Context'
import Spinner from './Spinner';

const Section2 = () => {
    const { Data, isLoading } = useGlobalContext();



    return (
        <>
            {Data.slice(3, 5).map((element,index) => {

                return <>
                    <section className="text-gray-600 body-font" >
                        <div className="container mx-auto flex px-5 sm:py-20 md:flex-row flex-col items-center">
                            <div className="lg:flex-grow md:w-1/2 lg:pr-24 md:pr-16 flex flex-col md:items-start md:text-left mb-16 md:mb-0 items-center text-center order-last md:order-first ">
                                <h1 className="title-font sm:text-4xl text-2xl my-4 font-medium text-gray-900">{element.title}
                                </h1>
                                <p className="mb-8 leading-relaxed">{element.description}</p>
                                <div className="flex justify-center">
                                    <button className="inline-flex text-white bg-indigo-500 border-0 py-2 px-6 focus:outline-none hover:bg-indigo-600 rounded text-lg">Buy Now</button>
                                    <button className="ml-4 inline-flex text-gray-700 bg-gray-100 border-0 py-2 px-6 focus:outline-none hover:bg-gray-200 rounded text-lg">Add to cart</button>
                                </div>
                            </div>
                            <div className="lg:max-w-xs lg:w-full md:w-1/2 sm:w-3/6 w-3/6 ">
                                <img className="object-cover object-center rounded  max-h-[30rem]" alt="hero" src={element.image} />
                            </div>
                        </div>
                    </section>
                </>
            })}
            {isLoading && <Spinner />}
        </>
    )
}

export default Section2